::-webkit-input-placeholder {
  color: gray;
}

::-ms-input-placeholder {
  color: gray;
}

::placeholder {
  color: gray;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.side-drawer {
  height: 100%;
  background: rgb(27, 115, 174);
  box-shadow: 1px 0 7px rgba(0, 0, 0, 0.5); 
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 20rem;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.sideDrawer__navigation {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.sideDrawer__navigation-list-item {
  margin: 0.5rem 0;
}

.sideDrawer__navigation-home-item {
  margin-bottom: 0.5rem;
}

.sideDrawer__navigation-home-item a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.sideDrawer__navigation-list-item a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: rgb(35, 137, 23);
}

@media (min-width: 769px) {
  .side-drawer {
    display: none;
  }
}
.footer {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  background: rgb(27, 115, 174);
}

.footer__Content {
  color: whitesmoke;
  margin-top: 0.25rem;
}

.footer__Content-text {
  margin: auto;
  font-size: 0.75rem;
}

.footer__Content-socialMedia {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.footer__Content-socialMedia a {
  color: whitesmoke;
  flex-basis: 4%;
  font-size: 1rem;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.footer__Content-socialMedia a:hover {
  color: rgb(35, 137, 23);
}

@media (min-width: 813px) {
  .footer__Content-text {
    margin: auto;
    font-size: 1rem;
  }
  
  .footer__Content-socialMedia {
    margin: 0.5rem 0;
  }

  .footer__Content-socialMedia a {
    font-size: 1.3rem;
    flex-basis: 1.5%;
  }
}

@media (max-width: 812px) and (orientation: portrait) {
  .footer__Content-socialMedia a {
    font-size: 1.3rem;
    flex-basis: 7%;
  }
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;
}
.form {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-flow: row wrap;
}

.form__header {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.75rem;
  color: rgb(27, 115, 174);
  flex-basis: 100%;
}

.registerForm {
  display: flex;
}

.registerForm__inputs {
  display: flex;
  flex-flow: column;
}

.registerForm__inputs-item {
  display: flex;
  flex-flow: column;
  flex-basis: 25%;
  height: 3rem;
  margin-bottom: 0.25rem;
}

.registerForm__inputs input {
  margin: 0 0.25rem;
  padding: 0;
  border: none;
  border-bottom: 2px solid rgb(27, 115, 174);
  background: transparent;
  color: black;
  font-size: 1rem;
  padding: 0.5rem;
  outline: none;
}

.registerForm__inputs-item-errorMsg {
  font-size: 0.7rem;
  padding-top: 0.3rem;
  color: red;
}

.registerForm__buttonSpace {
  justify-content: center;
  display: flex;
  align-items: center;
  margin-left: 1rem;
  height: 14.625rem;
}

.registerForm__buttonSpace button {
  border: 0px;
  background: none;
  margin: 1.7rem 0 0 0.5rem;
  color: rgb(27, 115, 174);
  width: 40px;
  height: 40px;
  font-size: 1.5rem;
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

@media (min-width: 769px) {
  .form {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  .registerForm__inputs input {
    font-size: 1rem;
  }
}
.formContainer {
  margin-top: 4rem;
  width: 100%;
  height: 100%;
}

.loginForm__header {
  font-size: 1.5rem;
  font-weight: bold;
  color: rgb(27, 115, 174);
  flex-basis: 100%;
  margin-bottom: 0.75rem;
}

.loginForm {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-flow: row wrap;
}

.loginForm form {
  display: flex;
}

.inputs {
  display: flex;
  flex-flow: column;
  flex-basis: 20%;
}

.inputs * {
  margin: 0 0 0.5rem 0;
  padding: 0;
  border: none;
  border-bottom: 2px solid rgb(27, 115, 174);
  background: transparent;
  color: black;
  font-size: 1rem;
  padding: 0.5rem;
  outline: none;
}

.buttonSpace {
  justify-content: center;
  display: flex;
  align-items: center;
  margin-left: 1rem;
}

.buttonSpace button {
  border: 0px;
  background: none;
  margin: 1.7rem 0 0 0.5rem;
  color: rgb(27, 115, 174);
  width: 40px;
  height: 40px;
  font-size: 1.5rem;
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.buttonSpace button:hover {
  color: #243b55;
}

@media (min-width: 813px) {
  .loginForm {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
}

@media (max-width: 812px) and (orientation: portrait) {
  .loginForm {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
}

@media (max-width: 812px) and (orientation: landscape) {
  .loginForm {
    margin-top: 0.75rem;
  }
}
.side-drawer-toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 1.5rem;
  width: 1.5rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.side-drawer-toggle-button:focus {
  outline: none;
}

.side-drawer-toggle-button__line {
  width: 1.5rem;
  height: 2px;
  background: white;
}
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgb(27, 115, 174);
  height: 3rem;
}

.navbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.navbar__logo {
  margin-left: 0.5rem;
}

.navbar__logo a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.distance {
  flex: 1 1;
}

.navbar__navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar__navigation-items li {
  padding: 0 0.5rem;
}

.navbar__navigation-items a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

.navbar__navigation-items a:hover,
.navbar__navigation-items a:active {
  color: rgb(35, 137, 23);
}

@media (max-width: 768px) {
  .navbar__navigation-items {
    display: none;
  }

  .navbar__logo {
    display: none;
  }
}

@media (min-width: 769px) {
  .navbar__toggle-button {
    display: none;
  }

  .navbar__logo {
    margin-left: 0;
  }
}
.profile {
  display: flex;
  flex-flow: column wrap;
  color: whitesmoke;
  border-radius: 1rem;
  border: 1px solid rgb(27, 115, 174);
  background: rgb(27, 115, 174);
  width: 100%;
}

.title {
  flex-basis: 100%;
  margin-bottom: 1rem;
}

.profile__items {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin: 1rem 0 1rem 0;
  font-size: 0.9rem;
}

.profile__items-item {
  flex-basis: 50%;
  margin-top: 0.5rem;
}
.Book {
  margin-top: 1rem;
  border-radius: 1rem;
  color: whitesmoke;
  border: 1px solid rgb(27, 115, 174);
  background: rgba(27, 115, 174, 0.9);
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.Book > div {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-basis: 24%;
  padding: 0.5rem 0;
}

.Book-button button {
  color: whitesmoke;
  border: 0px;
  background: none;
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  font-size: 1.1rem;
}

.Book-button button:hover {
  color: rgb(35, 137, 23);
}

